import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';

import { LoginService } from './login.service';
import { AdminUser } from '../shared/models/AdminUser';
import { DatasourceService } from '../shared/services/datasource.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {

  adminUser: AdminUser;
  adminUserList: Array<AdminUser>;

  constructor(private readonly router: Router, private readonly loginService: LoginService,
    private datasource: DatasourceService) {
    this.adminUser = new AdminUser();
  }

  submitLoginForm(loginForm: NgForm): void {
    this.loginService.getUsersList().subscribe(
      (adminUserList) => {
        this.adminUserList = adminUserList;
      }, (error) => console.error(error),
      () => {
        if (this.isAdminUserPrsesent(loginForm.value.username, loginForm.value.password)) {
          this.loginSuccessAlert();
        } else {
          this.loginErrorAlert();
        }
      });
  }

  isAdminUserPrsesent(username: string, password: string): boolean {
    for (let adminUser of this.adminUserList) {
      if (adminUser.username === username) {
        if (adminUser.password === password) {
          this.adminUser = adminUser;
          return true;
        }
      }
    }
    return false;
  }

  resetLoginForm(loginForm: NgForm): void {
    loginForm.reset();
  }

  loginSuccessAlert(): void {
    Swal.fire({
      type: 'success',
      title: `${this.adminUser.name} logged in successfully.`,
      showConfirmButton: false,
      timer: 1500
    }).then(() => {
      this.datasource.setAdminUser(this.adminUser);
      localStorage.setItem('adminUser', JSON.stringify(this.adminUser));
      this.router.navigateByUrl('studentOnboardForm')
    });
  }

  loginErrorAlert(): void {
    Swal.fire({
      type: 'error',
      title: `Please enter valid username or password.`,
      showConfirmButton: true
    })
  }

}
