import { Component, Input, Output, EventEmitter } from '@angular/core';

import { Student } from '../../../students/models/Student';
import { Constants } from '../../constants/Constants';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.css']
})
export class FilterComponent {

  filterText: string;
  @Input() studentList: Array<Student>;
  @Output() filteredStudentList: EventEmitter<Array<Student>>;

  constructor() {
    this.filteredStudentList = new EventEmitter<Array<Student>>();
    this.filterText = 'All';
  }

  filterStudentList(): void {
    let filteredList = new Array<Student>();
    if (this.filterText === Constants.ALL) {
      this.filteredStudentList.emit(this.studentList);
    } else {
      for (let student of this.studentList) {
        if (student.category === this.filterText) {
          filteredList.push(student);
        }
      }
      this.filteredStudentList.emit(filteredList);
    }
  }
}
