import { Pipe, PipeTransform } from '@angular/core';

import { Student } from '../../../app/students/models/Student';

@Pipe({
  name: 'searchStudent'
})
export class SearchStudentPipe implements PipeTransform {

  transform(studentList: Array<Student>, studentName?: string): Array<Student> {
    if (studentName) {
      let filteredList: Array<Student> = new Array<Student>();
      for (let student of studentList) {
        if (student.name.toLowerCase().includes(studentName.toLowerCase())) {
          filteredList.push(student);
        }
      }
      return filteredList;
    }
    return studentList;
  }

}
