import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import Swal from 'sweetalert2';

import { DocumentService } from '../../services/document.service';
import { StudentService } from '../../services/student.service';
import { Student } from '../../models/Student';
import { DatasourceService } from '../../../shared/services/datasource.service';
import { Constants } from '../../../shared/constants/Constants';

@Component({
  selector: 'app-student-onboard-form',
  templateUrl: './student-onboard-form.component.html',
  styleUrls: ['./student-onboard-form.component.css']
})
export class StudentOnboardFormComponent implements OnInit, OnDestroy {
  isDocumentsValid: boolean = false;
  documentList: Array<string>;
  mandatoryDocumentsList: Array<string>;
  categorySelected: string;
  savedStudent: Student;
  isDisabled: boolean;
  categories: Array<string>;
  studentOnboardForm: FormGroup;

  constructor(private formBuilder: FormBuilder, private readonly documentService: DocumentService, private readonly datasource: DatasourceService,
    private readonly studentService: StudentService, private readonly router: Router, private readonly route: ActivatedRoute) {
    this.isDisabled = false;
    this.studentOnboardForm = this.formBuilder.group({
      name: ['', Validators.required],
      category: ['', [Validators.required]],
      documents: this.formBuilder.array([]),
      dateOfBirth: ['', [Validators.required]],
      fatherName: ['', [Validators.required]],
      motherName: ['', [Validators.required]],
      lastClassScore: ['', [Validators.required]]
    });
    this.categories = [Constants.DOMESTIC, Constants.INTERNATIONAL];
  }

  ngOnInit() {
    this.documentService.fetchAllDocuments().subscribe((value) => {
      this.documentList = value.documents;
    });
    this.savedStudent = JSON.parse(localStorage.getItem('savedStudent'));
    this.datasource.getStudent().subscribe((value) => {
      this.savedStudent = value;
    })
    if (this.savedStudent) {
      this.makeFormGroup(this.savedStudent);
    }
    if (this.route.snapshot['_routerState'].url.toString().includes('view')) {
      this.isDisabled = true;
    }
  }

  ngOnDestroy() {
    this.savedStudent = null;
    this.datasource.setStudent(null);
  }

  get name() {
    return this.studentOnboardForm.get('name');
  }

  get category() {
    return this.studentOnboardForm.get('category');
  }

  get dateOfBirth() {
    return this.studentOnboardForm.get('dateOfBirth');
  }

  get fatherName() {
    return this.studentOnboardForm.get('fatherName');
  }

  get motherName() {
    return this.studentOnboardForm.get('motherName');
  }

  get lastClassScore() {
    return this.studentOnboardForm.get('lastClassScore');
  }

  get documents() {
    return this.studentOnboardForm.get('documents') as FormArray;
  }

  addDocuments() {
    this.documents.push(this.formBuilder.control(''));
  }

  makeFormGroup(student: Student): void {
    this.studentOnboardForm.patchValue({
      name: student.name,
      category: student.category,
      dateOfBirth: student.dateOfBirth,
      fatherName: student.fatherName,
      motherName: student.motherName,
      lastClassScore: student.lastClassScore
    })
    this.studentOnboardForm.setControl('documents', this.setDocumentArray(student.category, student.documents))
  }

  setDocumentArray(category: string, documents: Array<string>): FormArray {
    let docs = this.documents;
    let endIndex;
    let startIndex = 0
    if (category == Constants.DOMESTIC) {
      endIndex = 3;
    } else endIndex = 5;
    for (let doc of documents) {
      if (doc.length > 0) {
        if (startIndex <= endIndex) {
          docs.push(this.formBuilder.control('true', Validators.required));
        } else {
          docs.push(this.formBuilder.control(''));
        }
      } else {
        docs.push(this.formBuilder.control(''));
      }
      startIndex++;
    }
    return docs;
  }

  getDocumentsForCategory(): void {
    if (!this.isDisabled) {
      let docs: FormArray = this.documents;
      docs.controls = [];
      this.categorySelected = this.studentOnboardForm.value.category;
      this.documentService.fetchDocuments(this.categorySelected).subscribe((value) => {
        this.mandatoryDocumentsList = value.documents;
        for (let i = 0; i < this.mandatoryDocumentsList.length; i++) {
          docs.push(this.formBuilder.control('', Validators.required));
        }
        for (let i = 0; i < this.documentList.length - this.mandatoryDocumentsList.length; i++) {
          docs.push(this.formBuilder.control(''));
        }
      })
    }
  }

  submitStudentForm(): void {
    if (!this.isDisabled) {
      if (this.savedStudent) {
        console.log('student saved : ' + JSON.stringify(this.savedStudent));
        this.studentService.editStudentForm(this.studentOnboardForm, this.savedStudent.id);
      } else {
        this.studentService.submitStudentForm(this.studentOnboardForm, this.createDocumentList(this.studentOnboardForm.value.documents));
      }
      this.formSubmitSuccessAlert();
    }
  }

  formSubmitSuccessAlert(): void {
    Swal.fire({
      type: 'success',
      title: `${this.studentOnboardForm.value.name} onboarded successfully.`,
      showConfirmButton: false,
      timer: 1300
    }).then(() => {
      this.router.navigate(['/studentList']);
    })
  }

  createDocumentList(documents: Array<any>): Array<string> {
    let documentList = new Array<string>();
    for (let i = 0; i < documents.length; i++) {
      if (documents[i] == true) {
        documentList.push("true");
      } else {
        documentList.push("false");
      }
    }
    return documentList;
  }

  isDocumentArrayValid(): void {
    this.isDocumentsValid = true;
    let endIndex;
    if (this.category.value === Constants.DOMESTIC) {
      endIndex = 3;
    } else endIndex = 5;
    let docs = this.documents;
    for (let i = 0; i <= endIndex; i++) {
      if (!docs.controls[i].valid) {
        this.isDocumentsValid = false;
        return;
      }
    }
  }
}
